html body .container .touch-buttons {
  display: flex;
  flex-wrap: wrap;
  height: 100dvh;
  left: 0;
  max-width: 100% !important;
  opacity: 0;
  position: fixed;
  top: 0;
  transition: all 0.5s;
  width: 100vw;
  z-index: -1;
}
@media only screen and (max-width: 480px) {
  html body .container .touch-buttons.do-mobile {
    z-index: 9;
  }
}
@media only screen and (max-width: 480px) {
  html body .container .touch-buttons.engage {
    opacity: 0.4;
    z-index: 99;
  }
}
html body .container .touch-buttons span {
  width: 33%;
  height: 33%;
  position: relative;
}
html body .container .touch-buttons span:before {
  background-size: contain !important;
  content: "";
  height: 140px;
  left: 30%;
  position: absolute;
  top: 10%;
  width: 30px;
}
html body .container .touch-buttons span:first-child:before {
  background: url("../../src/images/sword-arrow.png") no-repeat;
  transform: rotate(125deg);
}
html body .container .touch-buttons span:nth-child(2):before {
  background: url("../../src/images/sword-arrow.png") no-repeat;
  transform: rotate(180deg);
}
html body .container .touch-buttons span:nth-child(3):before {
  background: url("../../src/images/sword-arrow.png") no-repeat;
  transform: rotate(225deg);
}
html body .container .touch-buttons span:nth-child(4):before {
  background: url("../../src/images/sword-arrow.png") no-repeat;
  transform: rotate(90deg);
}
html body .container .touch-buttons span:nth-child(6):before {
  background: url("../../src/images/sword-arrow.png") no-repeat;
  transform: rotate(270deg);
}
html body .container .touch-buttons span:nth-child(7):before {
  background: url("../../src/images/sword-arrow.png") no-repeat;
  transform: rotate(45deg);
}
html body .container .touch-buttons span:nth-child(8):before {
  background: url("../../src/images/sword-arrow.png") no-repeat;
}
html body .container .touch-buttons span:nth-child(9):before {
  background: url("../../src/images/sword-arrow.png") no-repeat;
  transform: rotate(315deg);
}
