html {
  overscroll-behavior: none;
  body {
    overflow: hidden;
    overscroll-behavior: none;
    width: 5000px;

    header, #header, footer, #footer {
      display: none;
    }

    .explore-item-list {
      max-height: 80vh;
      overflow: scroll;

      .find-explore-item {
        &.selected {
          color: blue;
          cursor: pointer;
          font-weight: bold;
        }
      }
    }

    #explore-create-account,
    #explore-login-account {
      cursor: pointer;
    }
    .intro-video {
      height: 100%;
      left: -6000px;
      opacity: 0;
      position: fixed;
      top: 0;
      transition: opacity .3s;
      width: 100%;
      z-index: 9999;

      #skip-intro-video {
        background: #0a7aff;
        bottom: 3rem;
        border-radius: 8px;
        color: white;
        cursor: pointer;
        position: fixed;
        right: 3rem;
        padding: 1rem 2rem;
        z-index: 9;
      }

      video {
        height: 100%;
        width: 100%;
        object-fit: cover;
        position: relative;
        z-index: 1;
      }

      &.engage {
        left: 0;
        opacity: 1;
      }
    }

    .explainer-trigger {
      position: absolute;
    }

    .explainer-container {
      background: white;
      border: dashed 8px #00cee3;
      border-radius: 20px;
      font-size: 20px;
      left: -5000px;
      opacity: 0;
      padding: 1rem 1.5rem;
      position: absolute;
      transition: .3s opacity;
      transition: .3s opacity;
      z-index: -1;

      img {
        position: absolute;
      }

      &.engage {
        opacity: 1;
        z-index: 8;

        &:after {
          content: "âœ•";
          cursor: pointer;
          font-size: 28px;
          font-weight: bold;
          position: absolute;
          right: 5px;
          top: 5px;
        }
      }
    }

    #primary {
      overflow: hidden;
      overscroll-behavior: none;

      @media only screen and (max-width: 480px) {
        height: 100dvh;
        left: 0;
        position: fixed;
        top: 0;
        width: 100vw;
      }

      .indicator-icon {
        height: 15px;
        opacity: 0;
        position: absolute;
        transition: opacity .2s;
        width: 15px;

        &.engage {
          display: block;
          opacity: 1;
        }
      }
    }
    #primary > .container {
      margin-top: 0;
    }

    .next-mission {
      display: none;

      &.engage {
        display: block;
      }
    }

    #create-account, #login-account {
      cursor: pointer;

      &:hover {
        color: #00a0d2;
        font-weight: bold;
      }
    }

    .map-cutscene {
      display: flex;
      justify-content: space-between;
      position: fixed;
      bottom: -1500px;
      left: 50%;
      max-height: 300px;
      transform: translatex(-50%);
      transition: bottom .3s;
      width: 80vw;
      z-index: 99999999;

      &.engage {
        bottom: 20px;

        &[data-video="true"] {
          bottom: unset;
          height: 100vh;
          top: 0;
          width: 100%;
        }
      }

      .wp-block-orbem-paragraph-mp3 {
        align-self: center;
        background: white;
        flex-basis: 70%;
        padding: 20px 50px;
        border-radius: 20px;
        border: 20px #4ee7ff solid;
        display: none;

        &.engage {
          display: block;

          p {
            display: block;
          }
        }
      }

      .cut-character {
        opacity: 0;
        text-align: right;
        width: 300px;

        &.engage {
          opacity: 1;
        }

        img {
          height: 100%;
          margin-right: 1rem;
          object-fit: contain;
          object-position: bottom;
          width: 100%;
        }
      }
    }

    div[data-character="mc"] {
      opacity: 0;
      position: fixed;
      bottom: 0;
      left: 5%;
      height: 300px;
      text-align: right;
      width: 300px;

      &.engage {
        opacity: 1;
      }

      img {
        height: 100%;
        margin-right: 1rem;
        object-fit: contain;
        object-position: bottom;
        width: 100%;
      }
    }

    &.admin-bar {
      .explore-overlay {
        top: 184px;
      }
    }

    .grecaptcha-badge {
      display: none;
    }

    .explore-overlay {
      display: none;

      .you-died-message {
        display: none;
        left: 50%;
        max-width: 400px;
        position: absolute;
        text-align: center;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;

        &.engage {
          display: block;
        }
      }

      .greeting-message {
        display: none;
        left: 50%;
        max-width: 600px;
        position: absolute;
        text-align: center;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 100%;

        &.engage {
          display: block;
        }

        .greeting-buttons {
          display: flex;
          gap: 1rem;
          margin: auto;
          width: 224px;

          #engage-explore,
          #new-explore {
            display: none;
            opacity: 0;
            margin: 20px auto;
            padding: 10px 20px;
            transition: all .5s;

            &.engage {
              display: block;
              opacity: 1;
            }
          }
        }

        .character-choice {
          cursor: pointer;
          display: flex;
          justify-content: center;

          .character-item {
            cursor: pointer;
            width: 25%;

            img {
              height: auto;
              width: 100%;

              &.engage {
                border: 5px dotted deepskyblue;
              }
            }

            small {
              color: green;
              font-weight: 600;
            }
          }
        }

        button {
          margin-top: 15px;
        }
      }

      &.engage {
        display: block;
      }
    }
    .container {
      background-size: cover;
      background-repeat: no-repeat;
      background-position: left top;
      height: 4517px;
      overflow: hidden;
      overscroll-behavior: none;
      position: relative;
      width: 5000px;

      #new-addition {
        position: fixed;
        right: 6.75rem;
        top: 1rem;
        max-width: 700px;
        width: max-content;

        &:not(.engage) {
          width: 300px;
          z-index: 9;

          &:hover {
            &:before {
              font-size: 50px;
            }
          }

          &:before {
            content: 'ðŸ› ';
            color: black;
            cursor: pointer;
            line-height: 30px;
            text-shadow: 1px 1px 2px white;
            font-size: 50px;
            position: absolute;
            right: 0;
            top: 0;
          }
        }

        &.engage {
          z-index: 10;
          overflow: scroll;
          max-height: 95vh;

          .add-new-fields {
            width: 50%;
            input {
              border: 1px solid grey;
            }
          }

          .addition-content {
            display: block;

            ul {
              background: #c8c8c8;
              padding: 01.5rem;
              width: 40%;
              min-width: 171px;
              border-radius: 8px;
              li {
                padding-left: 0;
                cursor: pointer;

                &.engage {
                  color: blue;
                }

                &:hover {
                  font-weight: bold;
                }
              }
            }
          }
        }

        .addition-content {
          background: white;
          border: 1px solid black;
          border-radius: 10px;
          display: none;
          padding: 1rem;

          h2 {
            margin-top: 0;
          }

          .add-new-form {
            display: flex;
            flex-wrap: wrap;
            flex-direction: row;
            gap: 1rem;
            position: relative;
            width: 100%;

            .close-settings {
              cursor: pointer;
              position: absolute;
              right: .25rem;
              top: .25rem;
            }

            h2 {
              width: 100%;
            }

            a {
              margin-top: 1rem;
            }
          }
        }
      }

      #magic, #weapon {
        bottom: 1rem;
        left: 1rem;
        position: fixed;
        z-index: 1;

        .magic-container {
          display: flex;
        }

        img {
          object-fit: contain;
        }
      }

      #magic {
        margin-top: 2rem;
      }

      #weapon {
        bottom: 5.5rem;
      }

      #characters {
        position: fixed;
        right: 6.75rem;
        top: 1rem;

        &:not(.engage) {
          width: 300px;
          z-index: 9;

          &:hover {
            &:before {
              font-size: 50px;
            }
          }

          &:before {
            content: 'â›‘';
            color: black;
            cursor: pointer;
            line-height: 30px;
            text-shadow: 1px 1px 2px white;
            font-size: 50px;
            position: absolute;
            right: 0;
            top: 0;
          }
        }

        &.engage {
          z-index: 10;

          .characters-content {
            display: block;
          }
        }

        .characters-content {
          background: white;
          border: 1px solid black;
          border-radius: 10px;
          display: none;
          padding: 1rem;

          h2 {
            margin-top: 0;
          }

          .characters-form {
            display: flex;
            flex-direction: column;
            position: relative;
            width: 100%;

            .close-settings {
              cursor: pointer;
              position: absolute;
              right: .25rem;
              top: .25rem;
            }

            .character-list {
              display: flex;
              gap: 1rem;

              .character-item {
                cursor: pointer;
                width: 150px;

                &:hover {
                  border: 4px solid deepskyblue;
                  border-radius: 4px;
                }

                .character-images {
                  .character-icon {
                    display: none;

                    &.engage {
                      display: block;
                    }
                  }
                }

                img {
                  width: 100%;
                }
              }
            }

            label {
              margin-bottom: 1rem;
            }

            a {
              margin-top: 1rem;
            }
          }
        }
      }

      #settings {
        position: fixed;
        right: 1rem;
        top: 1rem;
        width: 300px;

        &:not(.engage) {
          z-index: 9;

          &:hover {
            &:before {
              font-size: 50px;
            }
          }

          &:before {
            content: 'âš™';
            color: black;
            cursor: pointer;
            line-height: 30px;
            text-shadow: 1px 1px 2px white;
            font-size: 50px;
            position: absolute;
            right: 0;
            top: 0;
          }
        }

        &.engage {
          z-index: 10;

          .setting-content {
            display: block;
          }
        }

        .setting-content {
          background: white;
          border: 1px solid black;
          border-radius: 10px;
          display: none;
          padding: 1rem;

          h2 {
            margin-top: 0;
          }

          .settings-form {
            display: flex;
            flex-direction: column;
            position: relative;
            width: 100%;

            .close-settings {
              cursor: pointer;
              position: absolute;
              right: .25rem;
              top: .25rem;
            }

            label {
              margin-bottom: 1rem;
            }

            a {
              margin-top: 1rem;
            }
          }
        }
      }

      #storage {
        position: fixed;
        right: 3.5rem;
        top: 1rem;
        width: 100%;

        &:not(.engage) {
          z-index: 9;

          &:hover {
            &:before {
              font-size: 50px;
            }
          }

          &:before {
            content: 'â–¦';
            color: black;
            cursor: pointer;
            line-height: 30px;
            text-shadow: 1px 1px 2px white;
            font-size: 50px;
            position: absolute;
            right: 0;
            top: 0;
          }
        }

        &.engage {
          z-index: 10;

          .storage-content {
            display: block;
          }
        }

        .storage-content {
          background: white;
          border: 1px solid black;
          border-radius: 10px;
          display: none;
          padding: 1rem;
          margin: auto;
          width: 600px;

          h2 {
            margin-top: 0;
          }

          .storage-form {
            display: flex;
            flex-direction: column;
            position: relative;
            width: 100%;

            .close-settings {
              cursor: pointer;
              position: absolute;
              right: .25rem;
              top: .25rem;
            }

            .retrieval-points {
              .storage-menu {
                border-left: 1px solid black;
                border-bottom: 1px solid black;
                display: none;
                flex-wrap: nowrap;

                &[data-menu="weapons"] {
                  .storage-item {
                    img {
                      object-fit: contain;
                      width: 80px;
                    }
                  }
                }

                &.engage {
                  display: flex;
                }

                .storage-item {
                  border-top: 1px solid black;
                  border-right: 1px solid black;
                  padding: 1rem;
                  width: 100%;

                  &:before {
                    content: attr(data-count);
                  }

                  &[data-type="health"] {
                    &:after {
                      content: 'â™¥';
                      font-size: 2rem;
                    }
                  }

                  &[data-type="mana"] {
                    &:after {
                      content: 'â‚';
                      font-size: 2rem;
                    }
                  }
                }
              }
            }

            label {
              margin-bottom: 1rem;
            }

            a {
              margin-top: 1rem;
            }
          }
        }
      }

      #return-to-map {
        cursor: pointer;
        display: none;
        position: fixed;
        right: 40px;
        top: 40px;
        z-index: 99999;

        &.engage {
          display: block;
        }
      }

      #leave-map {
        display: none;
        opacity: 0;
        position: fixed;
        right: 40px;
        top: 40px;
        z-index: 99999;

        &.engage {
          display: block;
          opacity: 1;
        }
      }

      #sound-control {
        bottom: 40px;
        cursor: pointer;
        height: 50px;
        opacity: 0;
        position: fixed;
        right: 40px;
        width: 50px;
        z-index: 99999;

        &:before {
          background: url('../../src/images/sound-on.svg') no-repeat 100%;
          background-size: 100% 100%;
          content: '';
          height: 40px;
          left: 0;
          position: absolute;
          top: 0;
          width: 40px
        }

        &.mute {
          &:before {
            background: url('../../src/images/sound-off.svg') no-repeat 100%;
            background-size: 100% 100%;
            width: 28px;
          }
        }
      }

      #missions {
        display: flex;
        flex-direction: column;
        gap: .5rem;
        left: 1rem;
        opacity: 0;
        position: fixed;
        text-shadow: 1px 1px 2px white;
        top: 7.5rem;
        z-index: 99999;
      }

      #explore-points {
        display: flex;
        flex-direction: column;
        gap: .5rem;
        left: 1rem;
        opacity: 0;
        position: fixed;
        top: 1rem;
        z-index: 99999;

        .point-bar {
          border: #2b2b2b 1px solid;
          border-radius: 20px;
          height: 10px;
          overflow: hidden;
          transition: all .5s;

          &.health-amount {
            .gauge {
              background: #38a223;
              height: 100%;
            }
          }

          &.mana-amount {
            .gauge {
              background: #7500e3;
              height: 100%;
            }
          }

          &.power-amount {
            .gauge {
              background: #00cee3;
              height: 100%;
              transition: all .3s;
            }
          }

          &.point-amount {
            height: 2px;
            .gauge {
              background: #e3bd00;
              height: 100%;
            }
          }

          &.health-amount {
            .gauge {
              background: #38a223;
              height: 100%;
            }
          }

          &.engage {
            border-color: gold;
          }
        }

        .point-info-wrap {
          border-bottom: 1px solid black;
          padding-bottom: .5rem;
          display: flex;
          gap: .75rem;
        }
      }

      #key-guide {
        bottom: 40px;
        opacity: 0;
        position: fixed;
        right: -240px;
        transition: opacity .6s;
        width: 200px;
        z-index: 99999;

        img {
          width: 100%;
        }

        &.engage {
          @media only screen and (min-width: 480px) {
            opacity: .8;
            right: 40px;
          }
        }
      }

      [data-genre="explore-sign"] {
        &>div, &>p, &>img:not(.sign-image), &>figure {
          height: 100%;
          left: 50%;
          max-height: 60vh;
          max-width: 70vw;
          overflow: scroll;
          padding: 2rem;
          position: fixed;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 95%;
        }

        &:not(.open-up) {
          &>div, &>p, &>img:not(.sign-image), &>figure {
            display: none;
          }
        }
      }

      .map-item,
      .enemy-item {
        position: absolute;
        z-index: 1;

        &.selected {
          border: 5px solid lightblue;
          border-radius: 4px;
        }

        &[data-passable="true"].passable {
          z-index: 0;
        }

        &[data-foreground="true"] {
          z-index: 2;
        }

        &[data-enemy-type="boss"] {
          margin: 0 1rem;

          svg {
            background: rgba(0, 0, 255, 0.17);
            border: 3px solid blue;
            border-radius: 10000px;
            opacity: 0;
            position: absolute;
            left: 8px;
            transition: all 18s linear;
            width: 30px;
          }

          &.pulse-wave-engage:not(.pulse-in) {
            svg {
              left: unset;
              opacity: 1;
              padding: 1000px;
              top: unset;
              transform: translate(-1000px, -1000px);
              z-index: 99999999;
            }
          }
        }

        .map-item-modal {
          background: #fff;
          border-radius: 55px;
          font-size: 14px;
          opacity: 0;
          right: 0;
          padding: 22px;
          position: absolute;
          top: 0;
          transform: translate(80%, -80%);
          transition: all .5s;
          width: 200px;

          &.engage {
            display: block;
          }

          &:before {
            border-top: 20px solid #fff;
            border-left: 0 solid transparent;
            border-right: 36px solid transparent;
            content: "";
            left: 20px;
            position: absolute;
            top: 91%;
            width: 0;
          }
        }

        &.engage {
          .map-item-modal {
            opacity: 1;
          }
        }
      }

      div[data-map-item-area] {
        position: absolute;
        height: 100%;
        left: 0;
        top: 0;
        width: 100%;
      }

      .default-map {
        &>svg {
          height: 4517px;
          left: 0;
          opacity: 0;
          position: absolute;
          top: 0;
          width: 5000px;
        }

        .minigame {
          display: none;
          position: fixed;
          left: 50%;
          top: 45%;
          width: 75%;
          height: auto;
          max-width: 700px;
          transform: translate(-50%, -50%);
          z-index: 1;

          &.engage {
            display: block;

            .wires {
              display: flex;
              position: fixed;
            }

            .programming-subject {
              display: none;

              &.engage {
                display: block;
              }
            }

            .minigame-programming {
              align-items: center;
              display: none;
              height: 75vh;
              justify-content: space-around;

              &.engage {
                display: flex;
              }

              .input-section {
                align-items: center;
                display: flex;
                flex-wrap: wrap;
                height: 100vh;
                justify-content: center;
                width: 66%;

                input {
                  width: 70%;
                }

                button {
                  width: 28%;
                }

                .programming-output {
                  background: #1f2023;
                  border: 2px solid #e3e3e3;
                  border-radius: 4px;
                  color: white;
                  height: 100%;
                  max-height: 65vh;
                  max-width: 500px;
                  padding: 2rem;
                  width: 100%;

                  textarea {
                    background: transparent;
                    border: 0;
                    color: #ffcb00;
                    outline: none;
                    resize: none;
                  }
                }
              }

              img {
                background: white;
                height: 68vh;
                float: right;
                padding: 1rem;
                border: 4px solid #00c1ff;
                border-radius: 4px;
                box-shadow: 5px 10px 9px -2px #00000075;
              }
            }
          }
        }

        .map-item,
        .enemy-item {
          position: absolute;
          z-index: 2;

          .character-icon {
            display: none;

            &.engage {
              display: block;
            }
          }
        }
      }

      #map-character {
        height: 600px;
        position: absolute;
        width: 800px;
        z-index: 1;

        @media only screen and (max-width: 480px) {
          width: 300px;
        }

        &:before {
          bottom: 30%;
          content: '+50pts';
          color: gold;
          left:47.5%;
          opacity: 0;
          position: absolute;
          transition: all .8s;
          z-index: 9;
        }

        &.point {
          &:before {
            bottom: 50%;
            opacity: 1;
          }
        }

        &.over {
          &:before {
            opacity: 0;
          }
        }

        .map-character-icon {
          display: none;
          height: auto;
          left: 50%;
          opacity: 1;
          position: absolute;
          top: 50%;
          transform: translate(-50%, -50%);
          width: 90px;

          &.engage {
            display: block;
          }

          &.punched {
            opacity: 0;
          }
        }
      }

      .map-weapon,
      .magic-weapon {
        position: absolute;

        &[data-weapon="fist"] {
          z-index: 1;
        }

        &.protection {
          &.engage {
            background: blue;
            border-radius: 10000px;
            opacity: .8;
            padding: 100px;
            transition: padding .1s;
            transform: translate( -100px, -100px );
            z-index: 100;
          }
        }

        &[data-direction="down"]:not(.protection):not(.fire) {
          &.engage {
            padding-bottom: 6rem;
            z-index: 1;

            &.heavy-engage {
              scale: 1.3;
            }
          }

          &.charge-engage {
            z-index: 1;
          }

          &.charge-attack-engage {
            z-index: 1;
          }
        }

        &[data-direction="top"]:not(.protection):not(.fire) {
          &[data-weapon="fist"] {
            z-index: 0;
          }

          z-index: 1;
          &.engage {
            padding-top: 7rem;
            transform: translateY(-7rem);
            width: 1rem;
            z-index: 0;

            &.heavy-engage {
              scale: 1.3;
            }
          }

          &.charge-engage {
            z-index: 0;
          }

          &.charge-attack-engage {
            z-index: 0;
          }
        }

        &[data-direction="left"]:not(.protection):not(.fire) {
          &[data-weapon="fist"] {
            z-index: 1;
          }

          &.engage {
            padding-left: 4.5rem;
            transform: translateX(-3.5rem);

            &.heavy-engage {
              scale: 1.3;
            }
          }

          &.charge-engage {
          }

          &.charge-attack-engage {
          }
        }

        &[data-direction="right"]:not(.protection):not(.fire) {
          &[data-weapon="fist"] {
            z-index: 0;
          }

          &.engage {
            padding-right: 3.5rem;

            &.heavy-engage {
              scale: 1.3;
            }
          }

          &.charge-engage {
          }

          &.charge-attack-engage {
          }
        }
      }

      .touch-buttons {
        display: flex;
        flex-wrap: wrap;
        height: 100dvh;
        left: 0;
        max-width: 100%!important;
        opacity: 0;
        position: fixed;
        top: 0;
        transition: all .5s;
        width: 100vw;
        z-index: -1;

        &.do-mobile {
          @media only screen and (max-width: 480px) {
            z-index: 9;
          }
        }

        &.engage {
          @media only screen and (max-width: 480px) {
            opacity: .4;
            z-index: 99;
          }
        }

        span {
          width: 33%;
          height: 33%;
          position: relative;

          &:before {
            background-size: contain!important;
            content: '';
            height: 140px;
            left: 30%;
            position: absolute;
            top: 10%;
            width: 30px;
          }

          &:first-child {
            &:before {
              background: url('../../src/images/sword-arrow.png') no-repeat;
              transform: rotate(125deg);
            }
          }
          &:nth-child(2) {
            &:before {
              background: url('../../src/images/sword-arrow.png') no-repeat;
              transform: rotate(180deg);
            }
          }
          &:nth-child(3) {
            &:before {
              background: url('../../src/images/sword-arrow.png') no-repeat;
              transform: rotate(225deg);
            }
          }
          &:nth-child(4) {
            &:before {
              background: url('../../src/images/sword-arrow.png') no-repeat;

              transform: rotate(90deg);
            }
          }
          &:nth-child(6) {
            &:before {
              background: url('../../src/images/sword-arrow.png') no-repeat;
              transform: rotate(270deg);
            }
          }
          &:nth-child(7) {
            &:before {
              background: url('../../src/images/sword-arrow.png') no-repeat;
              transform: rotate(45deg);
            }
          }
          &:nth-child(8) {
            &:before {
              background: url('../../src/images/sword-arrow.png') no-repeat;
            }
          }
          &:nth-child(9) {
            &:before {
              background: url('../../src/images/sword-arrow.png') no-repeat;
              transform: rotate(315deg);
            }
          }
        }
      }
    }

    .loading-screen {
      background: lightblue;
      display: none;
      height: 100%;
      left: 0;
      position: fixed;
      top: 0;
      width: 100%;
      z-index: 999999;

      &.engage {
        display: block;
      }
    }
  }
}
